import React from 'react';
import FeatherIcon from './FeatherIcon';

export default function Modal(props) {
  return (
    <div className="modal">
      <div className="modal__content">
        <button
          type="button"
          className="modal__btn--close modal__icon-btn--close"
          onClick={() => props.onModalClose()}
        >
          <FeatherIcon name="x" />
        </button>
        <div className="modal__content--left">
          <div className="modal__img-wrapper">
            <img className="modal__img" src={props.beer.imageURL} />
          </div>
        </div>
        <div className="modal__content--right">
          <h2 className="modal__title">{props.beer.name}</h2>
          <p className="modal__tagline">{props.beer.tagline}</p>
          <p className="modal__desc">{props.beer.description}</p>
          <input
            type="checkbox"
            id={`modalCheckbox-${props.beer.id}`}
            className="modal__input-favorite"
            name="favorites"
            checked={props.beer.isFavorite}
            onChange={() => props.onHeartClick(`${props.beer.id}`)}
          />
          <label htmlFor={`modalCheckbox-${props.beer.id}`} className="modal__label-favorite">
            <FeatherIcon name="heart" />
            {`${props.beer.isFavorite ? 'Remove from' : 'Add to'} Favorites`}
          </label>
          <ul className="modal__list">
            <li>
              <ul className="modal__sublist">
                <li className="modal__item">
                  <span className="modal__label">ABV:</span>
                  {props.beer.abv}
                </li>
                <li className="modal__item">
                  <span className="modal__label">EBC:</span>
                  {props.beer.ebc}
                </li>
                <li className="modal__item">
                  <span className="modal__label">IBU:</span>
                  {props.beer.ibu}
                </li>
              </ul>
            </li>
            <li className="modal__item">
              <span className="modal__label">Food Pairings:</span>
              {props.beer.foodPairings.join(', ')}
            </li>
            <li className="modal__item">
              <span className="modal__label">Malt:</span>
              {props.beer.ingredients.malt.map((item) => item.name).join(', ')}
            </li>
            <li className="modal__item">
              <span className="modal__label">Hops:</span>
              {props.beer.ingredients.hops.map((item) => item.name).join(', ')}
            </li>
            <li className="modal__item">
              <span className="modal__label">Yeast:</span>
              {props.beer.ingredients.yeast}
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
