import * as types from './actionTypes';

// reducers hold the store's state (the initialState object defines it)
// reducers also handle plain object actions and modify their state (immutably) accordingly
// this is the only way to change the store's state

const initialState = {
  beersArray: [],
  beersById: {},
  favoritedBeerIds: [],
  query: '',
  filters: {
    abv: {
      mild: { isActive: true, value: 'mild', min: 0, max: 3.9 },
      medium: { isActive: true, value: 'medium', min: 4, max: 7.5 },
      high: { isActive: true, value: 'high', min: 7.6, max: null }
    },
    ebc: {
      straw: { isActive: true, value: 'straw', min: 4, max: 7 },
      gold: { isActive: true, value: 'gold', min: 8, max: 17 },
      amber: { isActive: true, value: 'amber', min: 18, max: 38 },
      brown: { isActive: true, value: 'brown', min: 39, max: 78 },
      black: { isActive: true, value: 'black', min: 79, max: null }
    },
    ibu: {
      min: 0,
      max: null
    }
  },
  selectedBeerId: ''
};

export default function reduce(state = initialState, action = {}) {
  switch (action.type) {
    case types.FETCH_BEERS_SUCCESS:
      return {
        ...state,
        beersArray: action.beersArray,
        beersById: action.beersById
      };
    case types.LOAD_FAVORITED_BEERS:
      return {
        ...state,
        favoritedBeerIds: state.favoritedBeerIds.concat(action.favoritedBeerIds)
      };
    case types.ADD_TO_FAVORITES:
      return {
        ...state,
        favoritedBeerIds: state.favoritedBeerIds.concat(action.beerId)
      };
    case types.REMOVE_FROM_FAVORITES:
      return {
        ...state,
        favoritedBeerIds: state.favoritedBeerIds.filter((id) => id !== action.beerId)
      };
    case types.SET_FILTER:
      const { type, key, val } = action.filter;
      const isBool = typeof val === 'boolean';
      return {
        ...state,
        filters: {
          ...state.filters,
          [type]: {
            ...state.filters[type],
            [key]: !isBool
              ? val
              : {
                  ...state.filters[type][key],
                  isActive: val
                }
          }
        }
      };
    case types.SUBMIT_QUERY:
      return {
        ...state,
        query: action.query
      };
    case types.SELECT_BEER:
      return {
        ...state,
        selectedBeerId: action.beerId
      };
    case types.CLEAR_SELECTED_BEER:
      return {
        ...state,
        selectedBeerId: action.beerId
      };
    default:
      return state;
  }
}

// the selectors below are pure functions
// that take the global state as an argument
// and returns some transformation over it for easier consumption by views

export function getBeers(state) {
  const beersArray = state.beers.beersArray;
  const beersById = state.beers.beersById;
  const beerIdsArray = Object.keys(state.beers.beersById);
  return [beersArray, beersById, beerIdsArray];
}

export const getBeerIdsArray = (state) => {
  return Object.keys(state.beers.beersById);
};

export const getFavoritedBeerIds = (state) => {
  return state.beers.favoritedBeerIds;
};

export const getFavoritedBeersCount = (state) => {
  const favoritedBeers = state.beers.favoritedBeerIds;
  return favoritedBeers.length;
};

// Converts array to object, with the beer names as keys and data objects as values
export const getFavoritedBeersById = (state) => {
  const beersById = state.beers.beersById;
  const favoritedBeersById = state.beers.favoritedBeerIds.map((id) => ({
    [id]: beersById[id]
  }));
  return Object.assign({}, ...favoritedBeersById);
};

export const getRatedBeers = (state) => {
  const beersArray = state.beers.beersArray;
  const favoritedBeerIds = state.beers.favoritedBeerIds;
  return beersArray.map((beer) => ({
    ...beer,
    isFavorite: favoritedBeerIds.indexOf(`${beer.id}`) !== -1
  }));
};

export const getRatedBeersById = (state) => {
  const ratedBeers = getRatedBeers(state);
  return ratedBeers.reduce((ratedBeersById, beer) => {
    ratedBeersById[`${beer.id}`] = {
      name: beer.name,
      id: beer.id,
      tagline: beer.tagline,
      description: beer.description,
      imageURL: beer.imageURL,
      ingredients: beer.ingredients,
      foodPairings: beer.foodPairings,
      abv: beer.abv,
      ibu: beer.ibu,
      ebc: beer.ebc,
      isFavorite: beer.isFavorite
    };
    return ratedBeersById;
  }, {});
};

export const getFavoritedBeers = (state) => {
  const ratedBeers = getRatedBeers(state);
  return ratedBeers.filter((beer) => beer.isFavorite === true);
};

export const getQueryResults = (state) => {
  const ratedBeers = getRatedBeers(state);
  const query = state.beers.query.toLowerCase();
  const isObject = (obj) => obj === Object(obj);
  return ratedBeers.filter((beer) => {
    return Object.keys(beer).some((key) => {
      if (typeof beer[key] === 'string' && key !== 'imageURL') {
        return beer[key].toLowerCase().includes(query);
      }
      if (Array.isArray(beer[key])) {
        return beer[key].some((item) => item.toLowerCase().includes(query));
      }
      if (isObject(beer[key])) {
        return Object.values(beer[key]).some((item) => {
          if (Array.isArray(item)) {
            return item.some((subItem) => {
              return Object.values(subItem).some((val) => {
                if (typeof val === 'string') {
                  return val.toLowerCase().includes(query);
                }
              });
            });
          }
          if (typeof item === 'string') {
            return item.toLowerCase().includes(query);
          }
        });
      }
    });
  });
};

export const getFilteredBeers = (state, beers = getRatedBeers(state)) => {
  const { abv, ebc, ibu } = state.beers.filters;

  const getFilterRanges = (obj) => {
    return Object.values(obj).reduce((filterRanges, currentFilter) => {
      if (currentFilter.isActive) {
        let lastIndex = filterRanges.length - 1;
        const [prevMin, prevMax] = filterRanges[lastIndex] || [null, null];
        const [currMin, currMax] = [currentFilter.min, currentFilter.max];
        const isConsecutive = Math.ceil(prevMax) === Math.floor(currMin) || prevMax + 1 === currMin;
        if (lastIndex === -1 || !isConsecutive) {
          filterRanges.push([currMin, currMax]);
        } else {
          filterRanges[lastIndex] = [prevMin, currMax];
        }
      }
      return filterRanges;
    }, []);
  };

  const isWithinRange = (arr, num) => {
    return arr.some((range) => {
      const [min, max] = [...range];
      return num >= min && (max === null || num <= max);
    });
  };

  const ibuFilters = [[ibu.min, ibu.max]];
  const abvFilters = getFilterRanges(abv);
  const ebcFilters = getFilterRanges(ebc);

  return beers.filter((beer) => {
    return (
      isWithinRange(ibuFilters, beer.ibu) &&
      isWithinRange(abvFilters, beer.abv) &&
      isWithinRange(ebcFilters, beer.ebc)
    );
  });
};

export const getQuery = (state) => {
  return state.beers.query;
};

export const getFilters = (state) => {
  return state.beers.filters;
};

export const getSelectedBeerId = (state) => {
  return state.beers.selectedBeerId;
};

export const getFilteredBeersCount = (state) => {
  const filteredBeers = getFilteredBeers(state);
  return filteredBeers.length;
};

export const getQueryResultsCount = (state) => {
  const queryResults = getQueryResults(state);
  return queryResults.length;
};

export const getSelectedBeerDetails = (state) => {
  const ratedBeersById = getRatedBeersById(state);
  const selectedBeerId = state.beers.selectedBeerId;
  return ratedBeersById[selectedBeerId];
};

export const getFilteredQueryResults = (state) => {
  return getFilteredBeers(state, getQueryResults(state));
};
