import React, { Component } from 'react';
import ConnectedBeersContainer from '../containers/Beers';
import FeatherIcon from './FeatherIcon';
import SearchBar from './SearchBar';
import Modal from './Modal';
import { NavLink } from 'react-router-dom';
import logo from '../../assets/brewdog-logo.png';
import FilterBar from './FilterBar';

export default function AppView(props) {
  console.log(props.selectedBeer);
  return (
    <div className="app">
      <header className="app__header">
        <nav className="navbar">
          <div className="navbar__left">
            <NavLink exact to="/">
              <div className="logo">
                <img className="logo__img" src={logo} alt="Brewdog logo" />
              </div>
            </NavLink>
          </div>
          <div className="navbar__right">
            <div className="search-bar__wrapper clearfix">
              <SearchBar
                handleSearchSubmit={props.handleSearchSubmit}
                handleSearchClick={props.handleSearchClick}
                handleSearchChange={props.handleSearchChange}
                searchInputVal={props.searchInputVal}
                isExpanded={props.searchBarIsExpanded}
              />
            </div>
            <NavLink to="/favorites" className="navbar__link navbar__link--favorites">
              <div
                className={`navbar__icon-wrapper${props.favoritesCount === 0 ? ' hide-count' : ''}`}
                data-favorites-count={props.favoritesCount}
              >
                <FeatherIcon name="heart" />
              </div>
            </NavLink>
          </div>
        </nav>
      </header>
      {props.loadModal && (
        <Modal
          beer={props.selectedBeer}
          onModalClose={() => props.onModalClose()}
          onHeartClick={(id) => props.onHeartClick(id)}
        />
      )}
      <main className="app__main">
        <ConnectedBeersContainer>
          <FilterBar
            setFilter={props.setFilter}
            toggleHandler={props.toggleFilterBar}
            isVisible={props.filterBarIsVisible}
            filters={props.filters}
          />
        </ConnectedBeersContainer>
      </main>
    </div>
  );
}
