import React, { Component } from 'react';
import FeatherIcon from './FeatherIcon';

class SearchBar extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.searchTextInput = React.createRef();
  }

  handleClick(e) {
    e.stopPropagation();
    if (e.target.classList.contains('search-form__input') || this.props.searchInputVal !== '')
      return;
    if (!this.props.isExpanded) {
      this.searchTextInput.current.focus();
    }
    this.props.handleSearchClick();
  }

  handleChange(e) {
    this.props.handleSearchChange(e.target.value);
  }

  handleSubmit(e) {
    e.preventDefault();
    if (this.props.searchInputVal !== '') {
      this.props.handleSearchSubmit(this.props.searchInputVal);
    }
  }

  render() {
    return (
      <div
        onClick={(e) => this.handleClick(e)}
        className={`search-bar${this.props.isExpanded ? ' is-expanded' : ''}`}
      >
        <form className="search-form" onSubmit={(e) => this.handleSubmit(e)}>
          <input
            className="search-form__input"
            type="text"
            onChange={this.handleChange}
            value={this.props.searchInputVal}
            ref={this.searchTextInput}
          />
          <input className="search-form__submit" type="submit" value="" />
          <button type="button" className="btn search-form__btn">
            <FeatherIcon name="search" />
          </button>
        </form>
      </div>
    );
  }
}

export default SearchBar;
