import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as beersActions from '../store/beers/actions';
import * as beersSelectors from '../store/beers/reducer';
import BeersView from '../components/BeersView';
import FavoritesView from '../components/FavoritesView';
import SearchResults from '../components/SearchResults';
import { Route, Switch, withRouter } from 'react-router-dom';

class BeersContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPath: ''
    };
    this.handleRouteChange = this.handleRouteChange.bind(this);
    this.toggleFavorite = this.toggleFavorite.bind(this);
    this.updateSelectedBeer = this.updateSelectedBeer.bind(this);
  }

  componentWillMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      console.log('history', action, location.pathname);
    });
  }

  componentWillUnmount() {
    this.unlisten();
  }

  componentDidMount() {
    this.props.fetchBeers();
    this.props.getCachedFavoritedBeers();
    const { location } = this.props;
    if (this.state.currentPath !== location.pathname) {
      this.handleRouteChange(location.pathname);
    }
  }

  componentDidUpdate(prevProps) {
    const prevPath = prevProps.location.pathname;
    const currentPath = this.props.location.pathname;
    if (currentPath !== prevPath) {
      this.handleRouteChange(currentPath);
    }
  }

  toggleFavorite(id) {
    if (this.props.favoritedBeerIds.indexOf(id) === -1) {
      this.props.addToFavorites(id);
    } else {
      this.props.removeFromFavorites(id);
    }
  }

  handleRouteChange(path) {
    this.setState({
      currentPath: path
    });
  }

  updateSelectedBeer(beerId) {
    this.props.selectBeer(beerId);
  }

  render() {
    console.table(this.props.filteredBeers);
    console.table(this.props.ratedBeers);
    console.log(this.props.filters);
    const showFilteredList = this.props.ratedBeers.length !== this.props.filteredBeers.length;
    const showFilteredQueryResults =
      this.props.queryResults.length !== this.props.filteredQueryResults.length;
    return (
      <Switch>
        <Route
          exact
          path="/"
          render={(props) => (
            <BeersView
              beers={showFilteredList ? this.props.filteredBeers : this.props.ratedBeers}
              onHeartClick={(id) => this.toggleFavorite(id)}
              onCardClick={(id) => this.updateSelectedBeer(id)}
              location={props.location}
              filterBar={this.props.children}
              showFilteredList={showFilteredList}
            />
          )}
        />
        <Route
          path="/favorites"
          render={(props) => (
            <FavoritesView
              beers={this.props.favoritedBeers}
              onHeartClick={(id) => this.toggleFavorite(id)}
              location={props.location}
              onCardClick={(id) => this.updateSelectedBeer(id)}
              filterBar={this.props.children}
            />
          )}
        />
        <Route
          path="/search"
          render={(props) => (
            <SearchResults
              beers={
                showFilteredQueryResults ? this.props.filteredQueryResults : this.props.queryResults
              }
              onHeartClick={(id) => this.toggleFavorite(id)}
              location={props.location}
              searchQuery={this.props.query}
              onCardClick={(id) => this.updateSelectedBeer(id)}
              filterBar={this.props.children}
              showFilteredList={this.state.showFilteredList}
            />
          )}
        />
      </Switch>
    );
  }
}

const mapStateToProps = (state) => {
  const [beersArray, beersById, beerIdsArray] = beersSelectors.getBeers(state);
  return {
    beersArray,
    beersById,
    beerIdsArray,
    ratedBeers: beersSelectors.getRatedBeers(state),
    favoritedBeers: beersSelectors.getFavoritedBeers(state),
    favoritedBeerIds: beersSelectors.getFavoritedBeerIds(state),
    favoritedBeersById: beersSelectors.getFavoritedBeersById(state),
    filters: beersSelectors.getFilters(state),
    query: beersSelectors.getQuery(state),
    filteredBeers: beersSelectors.getFilteredBeers(state),
    queryResults: beersSelectors.getQueryResults(state),
    queryResultsCount: beersSelectors.getQueryResultsCount(state),
    filteredBeersCount: beersSelectors.getFilteredBeersCount(state),
    selectedBeerId: beersSelectors.getSelectedBeerId(state),
    selectedBeer: beersSelectors.getSelectedBeerDetails(state),
    filteredQueryResults: beersSelectors.getFilteredQueryResults(state)
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchBeers: () => dispatch(beersActions.fetchBeers()),
    addToFavorites: (beerId) => dispatch(beersActions.addToFavorites(beerId)),
    removeFromFavorites: (beerId) => dispatch(beersActions.removeFromFavorites(beerId)),
    getCachedFavoritedBeers: () => dispatch(beersActions.getCachedFavoritedBeers()),
    selectBeer: (beerId) => dispatch(beersActions.selectBeer(beerId)),
    setFilter: (filter) => dispatch(beersActions.setFilter(filter))
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(BeersContainer)
);
