import * as types from './actionTypes';
import PunkService from '../../services/punk';
import LocalStorageService from '../../services/localStorage';

export const fetchBeersSuccess = (beers) => {
  return {
    type: types.FETCH_BEERS_SUCCESS,
    beersById: beers.reduce((beersById, beer) => {
      beersById[`${beer.id}`] = {
        name: beer.name,
        id: beer.id,
        tagline: beer.tagline,
        description: beer.description,
        imageURL: beer.imageURL,
        ingredients: beer.ingredients,
        foodPairings: beer.foodPairings,
        abv: beer.abv,
        ibu: beer.ibu,
        ebc: beer.ebc
      };
      return beersById;
    }, {}),
    beersArray: beers
  };
};

export const fetchBeers = () => {
  return async (dispatch) => {
    try {
      const beers = await PunkService.getAllBeers();
      dispatch(fetchBeersSuccess(beers));
    } catch (error) {
      console.error(error);
    }
  };
};

export const addToFavorites = (beerId) => {
  return {
    type: types.ADD_TO_FAVORITES,
    beerId
  };
};

export const removeFromFavorites = (beerId) => {
  return {
    type: types.REMOVE_FROM_FAVORITES,
    beerId
  };
};

export const loadFavoritedBeers = (favoritedBeerIds) => {
  return {
    type: types.LOAD_FAVORITED_BEERS,
    favoritedBeerIds
  };
};

export const getCachedFavoritedBeers = () => {
  return (dispatch) => {
    try {
      const favoritedBeerIds = LocalStorageService.getCachedFavoritedBeers();
      dispatch(loadFavoritedBeers(favoritedBeerIds));
    } catch (error) {
      console.error(error);
    }
  };
};

export const setFilter = ({ type, key, val }) => {
  return {
    type: types.SET_FILTER,
    filter: {
      type,
      key,
      val
    }
  };
};

export const submitQuery = (query) => {
  return {
    type: types.SUBMIT_QUERY,
    query
  };
};

export const selectBeer = (beerId) => {
  return {
    type: types.SELECT_BEER,
    beerId
  };
};

export const clearSelectedBeer = () => {
  return {
    type: types.CLEAR_SELECTED_BEER,
    beerId: ''
  };
};
