import React, { Component } from 'react';
import FeatherIcon from './FeatherIcon';

function TabPane(props) {
  return (
    <div data-id={props.id} className="filter-bar__pane">
      <h3 className="filter-bar__pane-title">{`${props.title}:`}</h3>
      {props.content}
    </div>
  );
}

function CheckboxSet(props) {
  return <fieldset className="filter-bar__fieldset">{props.children}</fieldset>;
}

function FilterTab(props) {
  return (
    <div className={`filter-bar__tab ${props.isActive ? 'is-active' : ''}`}>
      <a href={`#${props.id}`} onClick={props.clickHandler}>
        {props.id}
        <span className="filter-bar__tab--full">({props.name})</span>
      </a>
      {props.children}
    </div>
  );
}

function CheckboxFilter(props) {
  return (
    <label htmlFor={props.id} className="filter-bar__label">
      <input
        type="checkbox"
        id={props.id}
        value={`${props.min}, ${props.max}`}
        className="filter-bar__checkbox"
        onChange={props.onFilterChange}
        checked={props.filterIsApplied}
      />
      {props.label}
    </label>
  );
}

function RangeSlider(props) {
  return (
    <div className="range-slider">
      <input
        type="range"
        className="range-slider__min"
        name="range-slider__min"
        min="0"
        max="100"
        step="10"
        value={props.min}
        onChange={props.minChangeHandler}
      />
      <div
        className="range-slider__output"
        style={{ width: 2 * (props.max - props.min), left: `${props.min}%` }}
        data-min={props.min}
        data-max={props.max === 100 ? '>100' : props.max}
      />
      <input
        type="range"
        className="range-slider__max"
        name="range-slider__max"
        min="0"
        max="100"
        step="10"
        value={props.max}
        onChange={props.maxChangeHandler}
      />
    </div>
  );
}

const filters = [
  {
    name: 'Alcohol',
    id: 'abv',
    inputType: 'checkbox',
    values: {
      mild: { index: 0, strength: 'mild', min: 0, max: 3.9, rangeStr: '< 4%' },
      medium: { index: 1, strength: 'medium', min: 4, max: 7.5, rangeStr: '4.0 - 7.5%' },
      high: { index: 2, strength: 'high', min: 7.6, max: null, rangeStr: '> 7.5%' }
    },
    subtitle: 'Filter by alcohol strength'
  },
  {
    name: 'Color',
    id: 'ebc',
    inputType: 'checkbox',
    values: {
      straw: { index: 0, color: 'straw', min: 4, max: 7, rangeStr: '4 - 7' },
      gold: { index: 1, color: 'gold', min: 8, max: 17, rangeStr: '8 - 17' },
      amber: { index: 2, color: 'amber', min: 18, max: 38, rangeStr: '18 - 38' },
      brown: { index: 3, color: 'brown', min: 39, max: 78, rangeStr: '39 - 78' },
      black: { index: 4, color: 'black', min: 79, max: null, rangeStr: '79+' }
    },
    subtitle: 'Filter by color'
  },
  {
    name: 'Bitterness',
    id: 'ibu',
    inputType: 'range',
    scale: {
      min: 0,
      max: 100,
      step: 10
    },
    subtitle: 'Filter by hops intensity'
  }
];

export default class FilterBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTab: null
    };
    this.clickHandler = this.clickHandler.bind(this);
    this.onFilterChange = this.onFilterChange.bind(this);
    this.minChangeHandler = this.minChangeHandler.bind(this);
    this.maxChangeHandler = this.maxChangeHandler.bind(this);
  }

  clickHandler(id, e) {
    e.preventDefault();
    this.setState((prevState) => ({
      currentTab: prevState.currentTab !== id ? id : null
    }));
  }

  onFilterChange(filterType, filterKey, e) {
    const value = e.target.checked === true;
    this.props.setFilter({ type: filterType, key: filterKey, val: value });
  }

  minChangeHandler(e) {
    const currentMax = this.props.filters.ibu.max === null ? 100 : this.props.filters.ibu.max;
    const newMin = +e.target.value;
    if (newMin < currentMax) {
      this.props.setFilter({ type: 'ibu', key: 'min', val: newMin });
    }
  }

  maxChangeHandler(e) {
    const newMax = +e.target.value;
    if (newMax > this.props.filters.ibu.min) {
      this.props.setFilter({ type: 'ibu', key: 'max', val: newMax === 100 ? null : newMax });
    }
  }

  render() {
    console.log(this.state.currentFilter);
    const checkboxOptions = (obj) => {
      const checkboxes = Object.keys(obj.values).map((key) => (
        <CheckboxFilter
          key={key}
          id={key}
          label={`${key} (${obj.values[key].rangeStr})`}
          min={obj.values[key].min}
          max={obj.values[key].max}
          filterIsApplied={this.props.filters[obj.id][key].isActive}
          onFilterChange={(e) => this.onFilterChange(obj.id, key, e)}
        />
      ));
      return checkboxes;
    };
    const filterTabs = filters.map((filter) => (
      <FilterTab
        clickHandler={(e) => this.clickHandler(filter.id, e)}
        key={filter.id}
        id={filter.id}
        name={filter.name}
        isActive={this.state.currentTab === filter.id}
      >
        {this.state.currentTab === filter.id && (
          <TabPane
            id={filter.id}
            title={filter.subtitle}
            content={
              filter.inputType === 'checkbox' ? (
                <CheckboxSet>{checkboxOptions(filter)}</CheckboxSet>
              ) : (
                <div className="range-slider__wrapper">
                  <RangeSlider
                    min={this.props.filters.ibu.min}
                    max={this.props.filters.ibu.max === null ? 100 : this.props.filters.ibu.max}
                    minChangeHandler={(e) => this.minChangeHandler(e)}
                    maxChangeHandler={(e) => this.maxChangeHandler(e)}
                  />
                </div>
              )
            }
          />
        )}
      </FilterTab>
    ));

    const currentFilter = filters.find((filter) => filter.id === this.state.currentTab);

    return (
      <div className="filter-bar">
        <button
          type="button"
          className={`btn filter-bar__btn--toggle ${this.props.isVisible ? 'is-active' : ''}`}
          onClick={() => this.props.toggleHandler()}
        >
          <FeatherIcon name="sliders" />
        </button>
        {this.props.isVisible && (
          <React.Fragment>
            <div className="filter-bar__tabs">{filterTabs}</div>
            <div className="filter-bar__content">
              {this.state.currentTab !== null && (
                <TabPane
                  id={this.state.currentTab}
                  title={currentFilter.subtitle}
                  content={
                    currentFilter.inputType === 'checkbox' ? (
                      <CheckboxSet>{checkboxOptions(currentFilter)}</CheckboxSet>
                    ) : (
                      <div className="range-slider__wrapper">
                        <RangeSlider
                          min={this.props.filters.ibu.min}
                          max={
                            this.props.filters.ibu.max === null ? 100 : this.props.filters.ibu.max
                          }
                          minChangeHandler={(e) => this.minChangeHandler(e)}
                          maxChangeHandler={(e) => this.maxChangeHandler(e)}
                        />
                      </div>
                    )
                  }
                />
              )}
            </div>
          </React.Fragment>
        )}
      </div>
    );
  }
}
