import React, { Component } from 'react';
import FeatherIcon from './FeatherIcon';

export default class BeerCard extends Component {
  constructor(props) {
    super(props);
    this.favoriteToggler = React.createRef();
    this.cardClickHandler = this.cardClickHandler.bind(this);
    this.heartClickHandler = this.heartClickHandler.bind(this);
  }

  cardClickHandler(id, e) {
    if (e.target !== this.favoriteToggler.current) {
      this.props.onCardClick(id);
    }
  }

  heartClickHandler(e) {
    e.stopPropagation();
  }

  render() {
    return (
      <div className="card" onClick={(e) => this.cardClickHandler(`${this.props.beer.id}`, e)}>
        <div className="card__header">
          <input
            type="checkbox"
            id={`cardCheckbox-${this.props.beer.id}`}
            className="card__input-favorite"
            name="favorites"
            checked={this.props.beer.isFavorite}
            onChange={() => this.props.onHeartClick(`${this.props.beer.id}`)}
            onClick={(e) => this.heartClickHandler(e)}
            ref={this.favoriteToggler}
          />
          <label
            htmlFor={`cardCheckbox-${this.props.beer.id}`}
            className="card__label-favorite"
            onClick={(e) => this.heartClickHandler(e)}
          >
            <FeatherIcon name="heart" />
          </label>
        </div>
        <div className="card__body">
          <div className="card__img-wrapper">
            <img className="card__img" src={this.props.beer.imageURL} alt={this.props.beer.name} />
          </div>
        </div>
        <div className="card__footer">
          <h4 className="card__title">{this.props.beer.name}</h4>
          <p className="card__tagline">{this.props.beer.tagline}</p>
          <ul className="card__details">
            <li className="card__detail">
              <span className="card__subheading">ABV:</span>
              {this.props.beer.abv}
            </li>
            <li className="card__detail">
              <span className="card__subheading">EBC:</span>
              {this.props.beer.ebc}
            </li>
            <li className="card__detail">
              <span className="card__subheading">IBU:</span>
              {this.props.beer.ibu}
            </li>
          </ul>
        </div>
      </div>
    );
  }
}
