import React, { Component } from 'react';
import AppView from '../components/AppView';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import * as beersActions from '../store/beers/actions';
import * as beersSelectors from '../store/beers/reducer';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      expandSearchBar: false,
      searchQuery: '',
      showFilterBar: true
    };
    this.initSearchResults = this.initSearchResults.bind(this);
    this.toggleSearchBar = this.toggleSearchBar.bind(this);
    this.toggleFilterBar = this.toggleFilterBar.bind(this);
    this.updateSearchInput = this.updateSearchInput.bind(this);
    this.toggleFavorite = this.toggleFavorite.bind(this);
  }

  componentDidUpdate(prevProps) {
    const prevPath = prevProps.location.pathname;
    const currentPath = this.props.location.pathname;
    if (currentPath !== prevPath) {
      if (prevPath === '/search') {
        this.updateSearchInput('');
        this.toggleSearchBar(false);
        this.props.submitQuery('');
      }
    }
  }

  initSearchResults(query) {
    this.props.submitQuery(query);
    this.props.history.push('/search');
  }

  toggleSearchBar(bool = null) {
    if (bool === null) {
      this.setState((state) => ({
        expandSearchBar: !state.expandSearchBar
      }));
    } else {
      this.setState({
        expandSearchBar: bool
      });
    }
  }

  toggleFilterBar(bool = null) {
    if (bool === null) {
      this.setState((state) => ({
        showFilterBar: !state.showFilterBar
      }));
    } else {
      this.setState({
        showFilterBar: bool
      });
    }
  }

  toggleFavorite(id) {
    if (this.props.favoritedBeerIds.indexOf(id) === -1) {
      this.props.addToFavorites(id);
    } else {
      this.props.removeFromFavorites(id);
    }
  }

  updateSearchInput(query) {
    this.setState({ searchQuery: query });
  }

  render() {
    return (
      <AppView
        handleSearchClick={this.toggleSearchBar}
        handleSearchSubmit={this.initSearchResults}
        handleSearchChange={this.updateSearchInput}
        favoritesCount={this.props.favoritesCount}
        searchInputVal={this.state.searchQuery}
        searchBarIsExpanded={this.state.expandSearchBar}
        filterBarIsVisible={this.state.showFilterBar}
        toggleFilterBar={this.toggleFilterBar}
        loadModal={this.props.selectedBeerId !== ''}
        selectedBeer={this.props.selectedBeer}
        onModalClose={() => this.props.clearSelectedBeer()}
        onHeartClick={(id) => this.toggleFavorite(id)}
        setFilter={(filter) => this.props.setFilter(filter)}
        filters={this.props.filters}
      />
    );
  }
}

const mapStateToProps = (state) => {
  const [beersArray, beersById, beerIdsArray] = beersSelectors.getBeers(state);
  return {
    beersArray,
    beersById,
    beerIdsArray,
    ratedBeers: beersSelectors.getRatedBeers(state),
    favoritesCount: beersSelectors.getFavoritedBeersCount(state),
    filteredBeers: beersSelectors.getFilteredBeers(state),
    queryResults: beersSelectors.getQueryResults(state),
    favoritedBeerIds: beersSelectors.getFavoritedBeerIds(state),
    filters: beersSelectors.getFilters(state),
    query: beersSelectors.getQuery(state),
    selectedBeerId: beersSelectors.getSelectedBeerId(state),
    selectedBeer: beersSelectors.getSelectedBeerDetails(state),
    filteredQueryResults: beersSelectors.getFilteredQueryResults(state)
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addToFavorites: (beerId) => dispatch(beersActions.addToFavorites(beerId)),
    removeFromFavorites: (beerId) => dispatch(beersActions.removeFromFavorites(beerId)),
    setFilter: (filter) => dispatch(beersActions.setFilter(filter)),
    submitQuery: (query) => dispatch(beersActions.submitQuery(query)),
    clearSelectedBeer: () => dispatch(beersActions.clearSelectedBeer())
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(App)
);
